import React, { Component } from "react";

import { Button, UncontrolledPopover, PopoverHeader, PopoverBody } from "reactstrap";

class RecipeInfoPopUp extends Component {
	constructor(props) {
		super(props);
		this.toggle = this.toggle.bind(this);
		this.state = {
			popoverOpen: false
		};
	}

	toggle() {
		this.setState({
			popoverOpen: !this.state.popoverOpen
		});
	}

	render() {
		return (
			<div style={{zIndex: "100", border: "2px solid black"}}>
				<Button id="PopoveerLegacy" type="button" onClick={this.toggle}>
					Launch Popover
				</Button>
				<UncontrolledPopover
				    style={{zIndex: "200", boder: "2px solid black"}}
					placement="right"
					isOpen={this.state.popoverOpen}
					target="Popover1"
					toggle={this.toggle}
				>
					<PopoverHeader>Popover Title</PopoverHeader>
					<PopoverBody>
						YUMM
					</PopoverBody>
				</UncontrolledPopover>
			</div>
		);
	}
}

export default RecipeInfoPopUp;



